export const select = `
  select
    S.id,
    S.userId,
    S.gradeId,
    S.isPublicSchool,
    S.schoolName,
    S.state,
    S.city,
    S.name,
    S.cpf,
    S.siteId,
    S.createdAt,
    S.modifiedAt,
    U.username,
    S.birthDate,
    S.gender
`;

export const from = `
  from students S
  join users U on U.id = S.userId
`;

export const subjects = `
  select
    S.id,
    S.name
  from userClasses as UC
    join subjects as S on S.id = UC.subjectId
  where UC.userId = ? and UC.subjectId
  order by S.id
`;

export const classTypes = `
  select
    CT.id,
    CT.name
  from userClasses as UC
    join classTypes as CT on CT.id = UC.classTypeId
  where UC.userId = ? and UC.classTypeId
  order by CT.id
`;
